import { translateCondition } from './helpers.js'
import Weather from './requests/weather.js'
import Gif from './requests/gif.js'
import state from './state.js'
import WeatherTemplate from './weatherTemplate.js'
import { renderWeather, renderMessage } from './render.js'

const Search = (input) => {
  const refresh = async () => {
    try {
      renderMessage.loading()

      const weatherInfo = await Weather(state.search, state.unit).getData()
      const gifUrl = await Gif(
        translateCondition(weatherInfo.condition),
      ).getUrl()
      const template = await WeatherTemplate(
        weatherInfo,
        gifUrl,
        state.unit,
      ).get()

      renderWeather.refresh(template)
      renderMessage.clear()
      clearInput()
    } catch (e) {
      state.search = state.previousSearch
      renderMessage.custom(e.message)
    }
  }

  const clearInput = () => {
    input.value = ''
  }

  const submit = async (e) => {
    state.previousSearch = state.search
    state.search = input.value

    e.preventDefault()
    await refresh()
  }

  return { submit, refresh }
}

const unit = (() => {
  const toggle = () => {
    if (state.unit === 'celsius') {
      state.unit = 'fahrenheit'
    } else {
      state.unit = 'celsius'
    }
  }

  return { toggle }
})()

export { Search, unit }
